import React, {Component} from 'react';
import { BackTop, Layout } from 'antd'
import NProgress from 'nprogress'
import HeaderLayout from '../../layouts/Header'
import ViewContent from "../../layouts/Main/ViewContent";
import MenuSider from '../../layouts/Main/MenuSider'
import {importComponent} from "../../components/import";



const panesList = [
  {
    title: 'Dashboard',
    content: <div><p>.</p></div>,
    key: 'dashboard',
  },
]


class Index extends Component {
  state = {
    collapsed: false,
    isMobile: false,
    activeKey: panesList.length > 0 ? panesList[0].key : '',
    panes:panesList,
  };



  onChange = activeKey => {
    console.log(activeKey)
    this.setState({activeKey})
  }

  onEdit = (targetKey, action) => {
    console.log(targetKey)
    console.log(action)
    this[action](targetKey)
  }

  remove = targetKey => {
    let { activeKey } = this.state
    const { panes } = this.state
    let lastIndex = 0
    panes.forEach((pane, i) => {
      if (pane.key === targetKey) {
        lastIndex = i - 1
      }
    })
    const PANES = panes.filter(pane => pane.key !== targetKey)
    if (PANES.length && activeKey === targetKey) {
      if (lastIndex >= 0) {
        activeKey = PANES[lastIndex].key
      } else {
        activeKey = PANES[0].key
      }
    }
    this.setState({ panes: PANES, activeKey })
  }

  add = (data) => {
    const { panes } = this.state
    const activeKey = data.key
    const content = importComponent(data.key, this.add)
    if (panes.filter(val => val.key === activeKey).length > 0) {
      this.setState({ panes, activeKey })
      this.onLoading()
    } else {
      panes.push({ title: data.title, content, key: activeKey })
      this.setState({ panes, activeKey })
      this.onLoading()
    }
  }

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  onLoading = () => {
    NProgress.start()
    setTimeout(() => {
      NProgress.done()
    }, 600)
  }

  render() {
    const {children} = this.props
    const { collapsed } = this.state;
    return (
      <>
        <Layout className='overflow-y-hidden'>
          <Layout.Header className='fixed w-full p-0 z-10'>
            <HeaderLayout />
          </Layout.Header>

          <Layout className='max-h-screen'>
            <Layout.Sider
              collapsible
              collapsed={collapsed}
              onCollapse={this.onCollapse}
              className='p-0 overflow-y-auto'
            >
              <MenuSider {...this.state} add={this.add} />
            </Layout.Sider>
            <Layout.Content className='overflow-auto relative top-16' >
              <ViewContent {...this.state} onEdit={this.onEdit} onChange={this.onChange}>
                {children}
              </ViewContent>
            </Layout.Content>
          </Layout>
        </Layout>
        <BackTop />
      </>
    );
  }
}

export default Index;
