import React, {Component} from 'react';
import { Tabs} from "antd";


class ViewContent extends Component {


  render() {
    const { TabPane } = Tabs
    const { panes, activeKey, onChange, onEdit } = this.props




    return (
      <>
        <div>
          <Tabs
            hideAdd
            onChange={onChange}
            activeKey={activeKey}
            type="editable-card"
            onEdit={onEdit}
          >
            {panes.map(pane => (
              <TabPane tab={pane.title} key={pane.key} closable className='w-full'>
                <div className='wrapper-content'>
                  {pane.content}
                </div>

              </TabPane>
            ))}
          </Tabs>
        </div>
      </>
    );
  }
}

export default ViewContent;
