import Dashboard from "../../pages/Dashboard";


import vars from '../../utils/helper/variable'
import PosModuleProperty from "../../pages/PosModule/Property/v1";


export function importComponent(key, /*  addTabs */) {
  if(key === vars.MENU_KEY_DASHBOARD) {
    return <Dashboard />
  }

  if(key === '401') {
    return <PosModuleProperty />
  }
}
