import React, {Component} from 'react';
import {Menu} from "antd";
import {Link} from "react-router-dom";

class MenuSider extends Component {

  state = {
    openKeys:''
  }

  onOpenChange = keys => {
    const { openKeys } = this.state;
    // submenu keys of first level
    const rootSubmenuKeys = ['dashboard', 'master', 'purchase', 'pos', 'report', 'finance', 'accounting'];

    const latestOpenKey = keys.find(key => openKeys.indexOf(key) === -1);
    if (rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      this.setState({openKeys: keys});
    } else {
      this.setState({openKeys: latestOpenKey ? [latestOpenKey] : []});
    }
  };


  render() {
    const {openKeys} = this.state
    const { add} = this.props



    const menu = [
      {
        title: "Dashboard",
        key:'101',
        link: 'dashboard',
        children: []
      },
      {
        title: "POS Module",
        key:'pos',
        children: [
          {
            title: "Property",
            key:'401',
          },
          {
            title: "API Setting",
            key:'402',
          },
        ]
      },
      {
        title: "Kasir",
        key:'kasir',
        children: []
      },
    ]


    return (
      <>
        <div className='h-16' />
        <Menu mode='inline' openKeys={openKeys} onOpenChange={this.onOpenChange}>
          {
            // eslint-disable-next-line array-callback-return
            menu.map(mn => {
              if(mn.children.length === 0) {
                return (
                  <Menu.Item onClick={() => add(mn)} title={mn.title}>
                    <Link to='/pos/auth'>
                      <span>{mn.title}</span>
                    </Link>

                  </Menu.Item>
                  )
              }
                if(mn.children.length > 0) {
                  return (
                    <Menu.SubMenu title={mn.title} key={mn.key}>
                      {
                        mn.children.map(ch => (
                          <Menu.Item onClick={() => add(ch)}>
                            <span>{ch.title}</span>
                          </Menu.Item>
                        ))
                      }
                    </Menu.SubMenu>
                  )
                }
            }
            )
          }
        </Menu>
      </>
    );
  }
}

export default MenuSider;
